.TableBlock {
  flex: 0.65;
  overflow-x: hidden;
  padding: 30px 0 0 30px;
  height: 100%;
}
.TableBlock-selectedArea {
  font-weight: bolder;
}
.TableBlock-lineChartsBlock {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 10px;
}
.TableBlock-lineCard {
  width: calc(49% - 15px);
  min-height: 200px;
  border-radius: 20px;
  border: 2px solid #e5e7e9;
  margin-right: 20px;
  margin-bottom: 15px;
  padding: 10px 0 0 10px;
}
.TableBlock-lineCard p {
  margin: 0 0 0 30px;
  font-weight: bolder;
}
.TableBlock-lineCard .lineCard-lastYear {
  font-weight: normal;
  font-size: 12px;
  margin: 0 0 0 30px;
  color: #999999;
}
.TableBlock-lineCard .result-block {
  display: flex;
  flex-direction: row;
  align-items: baseline;
  z-index: 10;
  position: absolute;
}
.TableBlock-lineCard .result-block .number {
  font-size: 30px;
  font-weight: lighter;
}
.TableBlock-lineCard .result-block .progress-block {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 0;
  margin-left: 20px;
}
.TableBlock-lineCard .result-block span {
  font-weight: bold;
}
.TableBlock .ant-card-body {
  padding: 0 10px 0 0 !important;
}
.TableBlock-circleCard {
  border-radius: 20px;
  border: 2px solid #e5e7e9;
  padding: 20px;
}
.TableBlock-circleCard .ant-card-body {
  padding: 0 !important;
  display: flex;
  align-items: center;
  line-height: 0;
}
.TableBlock-chartDescription {
  display: flex;
  flex-direction: column;
  margin-left: 30px;
}
.TableBlock-chartDescription p:first-of-type {
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 20px;
  line-height: 1;
}
.TableBlock-chartDescription p:last-of-type {
  font-size: 12px;
  color: #d6dbdf;
  font-weight: bolder;
  line-height: 0.5;
}
.TableBlock-number {
  font-size: 26px;
  font-weight: normal;
  margin-bottom: 15px;
  line-height: 1.5;
}
.TableBlock-brushChartsBlock {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 20px;
}
.TableBlock-brushChartsBlock-secondBlock {
  margin-right: 20px;
  width: calc(50% - 20px);
}
.TableBlock-BrushChart-first {
  border-radius: 20px;
  border: 2px solid #e5e7e9;
  padding: 20px 0 15px 20px;
  width: calc(49% - 15px);
  margin-right: 35px;
}
.TableBlock-BrushChart-first p:last-child {
  font-size: 12px;
  color: #d6dbdf;
  font-weight: bolder;
  margin-bottom: 0;
}
.TableBlock-brushCartDescription {
  position: relative;
}
.TableBlock-brushCartDescription p:first-of-type {
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 10px;
  line-height: 1;
}
.TableBlock-brushCartDescription .result-block {
  display: flex;
  flex-direction: row;
  align-items: center;
  z-index: 10;
}
.TableBlock-brushCartDescription .result-block .number {
  font-size: 30px;
  font-weight: lighter;
}
.TableBlock-brushCartDescription .result-block .progress-block {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 0;
  margin-left: 20px;
}
.TableBlock-brushCartDescription .result-block span {
  color: #229954;
  font-weight: bold;
}
.TableBlock-BrushChart-second {
  border-radius: 20px;
  border: 2px solid #e5e7e9;
  padding: 20px 0 15px 20px;
  margin-bottom: 20px;
}
.TableBlock-BrushChart-second .result-block {
  position: absolute;
}
.TableBlock-BrushChart-second .result-block span {
  color: #cb4335;
  font-weight: bold;
}
.TableBlock-circleChartWithLegendBlock {
  margin-bottom: 20px;
}
.TableBlock-circleChartWithLegend {
  width: calc(100% - 15px);
  border-radius: 20px;
  border: 2px solid #e5e7e9;
  position: relative;
}
.TableBlock-circleChartWithLegend p {
  position: absolute;
  top: 10px;
  left: 285px;
  font-size: 16px;
  font-weight: bold;
}
.TableBlock-circleChartWithLegend div > div > div canvas {
  margin-right: -100px;
}
.TableBlock-circleChartWithLegend div > div > div {
  display: flex !important;
  flex-direction: row !important;
  align-items: center !important;
}
