.Map {
  flex: 0.35;
  overflow: hidden;
  position: relative;
}
.Map-areaSelector {
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 1000;
  width: 300px;
}
.Map .ant-select-selector {
  border-radius: 10px !important;
  box-shadow: 0 0 10px #abb2b9 !important;
}
.Map .ant-select-lg {
  font-size: 13px !important;
}
.leaflet-container {
  background-color: #e5e7e9;
  width: 36.458vw;
  height: 100%;
}
.leaflet-control-attribution {
  display: none;
}
.toolTip {
  background: none;
  font-weight: bold;
  font-size: 20px;
  color: black;
  box-shadow: none;
  border: none;
  font-family: "Roboto", sans-serif;
}
.toolTip:before {
  display: none;
}
.ant-select-dropdown {
  border-radius: 10px !important;
  box-shadow: 0 0 10px #abb2b9 !important;
}
