.FilterBlock {
  flex: 0.65;
  overflow-x: hidden;
  padding: 30px 0 0 30px;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.FilterBlock-periodList {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.FilterBlock .Checkbox-container {
  margin-right: 30px;
}
.FilterBlock-areaAndPeriodBlock {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 95%;
  border-bottom: 1px solid #e5e7e9;
  padding: 6px;
  margin-bottom: 20px;
}
.FilterBlock-filterButtons {
  width: 95%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 20px;
}
.FilterBlock-filterButtons .item {
  width: 29%;
  position: relative;
}
.FilterBlock-filterButtons .item-filterList {
  display: flex;
  flex-direction: column;
  justify-content: center;
  border: 1px solid #e5e7e9;
  width: 95%;
  border-radius: 10px;
  box-shadow: 0 0 10px #abb2b9;
  padding: 20px 15px;
  position: absolute;
  top: 70px;
  background-color: #fff;
  z-index: 1000;
  transition: 5s;
}
.FilterBlock-filterButtons .item-applyFilterButton {
  background-color: #229954 !important;
  color: #fff !important;
  font-weight: bolder !important;
  border: 1px solid #229954 !important;
  margin-bottom: 10px;
}
.FilterBlock-filterButtons .item-removeFilterButton {
  background-color: #aeb6bf !important;
  color: #fff !important;
  font-weight: bolder !important;
  border: 1px solid #aeb6bf !important;
}
.FilterBlock-filterButtons .item .active {
  outline: 0;
  box-shadow: 0 0 10px #abb2b9 !important;
}
.FilterBlock-filterButtons .item-download {
  width: 7%;
}
.FilterBlock-filterButtons .ant-btn {
  border-radius: 10px !important;
  border: 1px solid #e5e7e9;
}
.FilterBlock-filterButtons .ant-btn-lg {
  font-size: 12px !important;
  color: #839192;
  font-weight: bold;
  height: 50px !important;
  width: 95% !important;
}
.FilterBlock-filterButtons .ant-btn-lg .anticon-filter {
  color: #b2babb !important;
  font-size: 15px !important;
}
.FilterBlock-dataBlock {
  width: 95%;
}
.FilterBlock-dataBlock .ant-card-body {
  padding: 15px 20px !important;
}
.FilterBlock-item {
  border-radius: 10px;
  border: 1px solid #e5e7e9;
  margin-bottom: 5px;
}
.FilterBlock-itemHeader {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border-bottom: 1px solid #e5e7e9;
  margin-bottom: 15px;
}
.FilterBlock-itemHeader p {
  font-weight: bold;
}
.FilterBlock-itemHeader .itemHeader-period {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-weight: bold;
  width: 50%;
}
.FilterBlock-itemHeader .itemHeader-period li {
  margin-right: 10px;
  list-style: none;
}
.FilterBlock-itemBody {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.FilterBlock-itemBody .Item-chart {
  width: 40%;
}
.FilterBlock-itemBody .itemBody-values {
  display: flex;
  width: 50%;
  flex-direction: column;
}
.FilterBlock-itemBody .itemBody-values .values-list {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 7px;
}
.FilterBlock-itemBody .itemBody-values li {
  margin-right: 10px;
  list-style: none;
}
.FilterBlock-itemBody .itemBody-markers {
  margin-left: 40px;
}
.FilterBlock-itemBody .itemBody-markers li {
  list-style: none;
  color: #999999;
}
.FilterBlock-itemBody .itemBody-markers li:nth-of-type(2)::before {
  content: "\2022";
  color: #229954;
  font-weight: bold;
  display: inline-block;
  width: 1em;
  margin-left: -1em;
}
.FilterBlock-itemBody .itemBody-markers li:nth-of-type(1)::before {
  content: "\2022";
  color: #fadb14;
  font-weight: bold;
  display: inline-block;
  width: 1em;
  margin-left: -1em;
  margin-bottom: 7px;
}
