.IndustryFilterBlock {
  flex: 0.35;
  overflow: hidden;
  position: relative;
  background-color: #E5E7E9;
  display: flex;
}
.IndustryFilterBlock .filterBlock {
  width: 100%;
  margin-top: 100px;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.IndustryFilterBlock-areaSelector {
  z-index: 1000;
  width: 70%;
  margin-bottom: 30px;
}
.IndustryFilterBlock .ant-select-selector {
  border-radius: 10px !important;
  box-shadow: 0 0 10px #ABB2B9 !important;
}
.IndustryFilterBlock .ant-select-lg {
  font-size: 13px !important;
}
.ant-select-dropdown {
  border-radius: 10px !important;
  box-shadow: 0 0 10px #ABB2B9 !important;
}
