.Checkbox-container {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 13px;
  user-select: none;
}
.Checkbox-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
.Checkbox-container input:checked ~ .Checkbox-checkmark {
  background-color: #229954;
  border: 1px solid #229954;
}
.Checkbox-container input:checked ~ .Checkbox-checkmark:after {
  display: block;
}
.Checkbox-container .Checkbox-checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  border: 1px solid #eee;
  background-color: #fff;
}
.Checkbox-container .Checkbox-checkmark:after {
  content: "";
  position: absolute;
  display: none;
  left: 7.1px;
  top: 4.1px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  transform: rotate(45deg);
}
.containerWithLine:after {
  display: block;
  content: '';
  height: 1px;
  width: 71px;
  background-color: #229954;
  position: absolute;
  top: 40px;
  left: 0;
}
